export class Point {

    public x: number = 0;
    public y: number = 0;

    constructor(x: number = 0, y: number = 0) {
        this.x = x;
        this.y = y;
    }


    public multiply(n: number): void {
        this.x *= n;
        this.y *= n;
    }


    public copy(): Point {
        return new Point(this.x, this.y);
    }


    public static mid(a: Point, b: Point): Point {
        return new Point((a.x + b.x) / 2, (a.y + b.y) / 2);
    }
}
