import { Point } from "./Point";

const PI_OVER_180: number = Math.PI / 180;

export class AngleArc {

    public position: Point;
    public radius: number;
    public start: number;
    public end: number;
    public active: boolean = false;

    constructor(position: Point, radius: number, start: number, end: number) {
        this.position = position;
        this.radius = radius;
        this.start = start;
        this.end = end;
    }


    public render(ctx: CanvasRenderingContext2D): void {
        if (!this.active) return;
        
        ctx.beginPath();
        ctx.lineWidth = 1;
        ctx.strokeStyle = "#000000";
        ctx.lineJoin = "miter";
        ctx.arc(this.position.x, this.position.y, this.radius, this.start * PI_OVER_180, this.end * PI_OVER_180);
        ctx.stroke();
    }
}
