import { Point } from "./Point";

export class Stage {

    protected rootEl: HTMLElement;
    protected aspectRatio: Point;
    protected bounds: Point;
    protected canvasEl: HTMLCanvasElement;
    protected ctx: CanvasRenderingContext2D;

    constructor(rootEl: HTMLElement, canvasEl: HTMLCanvasElement, ctx: CanvasRenderingContext2D, aspectRatio: Point) {
        this.rootEl = rootEl;
        this.canvasEl = canvasEl;
        this.aspectRatio = aspectRatio;
        this.ctx = ctx;
        this.bounds = new Point();
        this.ctx.imageSmoothingEnabled = false
        this.setBounds();
    }


    public context(): CanvasRenderingContext2D {
        return this.ctx;
    }


    public clear() {
        this.ctx.setTransform(1, 0, 0, 1, 0, 0);
        this.ctx.clearRect(0, 0, this.bounds.x, this.bounds.y);
    }

    public translate(offset: Point): void {
        this.ctx.setTransform(1, 0, 0, 1, 0, 0);
        this.ctx.translate(offset.x, offset.y);
    }


    public getBounds(): Point {
        return this.bounds.copy();
    }


    public clipY(n: number): void {
        this.canvasEl.setAttribute('height', n.toString());
    }


    protected rootElWidthFromDom() {
        return this.rootEl.offsetWidth;
    }


    protected setBounds() {
        this.bounds.x = Math.round(this.rootElWidthFromDom());
        this.bounds.y = Math.round(this.bounds.x / this.aspectRatio.x * this.aspectRatio.y);
        this.canvasEl.setAttribute('width', this.bounds.x.toString());
        this.canvasEl.setAttribute('height', this.bounds.y.toString());
    }
}
