import { AngleArc } from "./AngleArc";
import { Label } from "./Label";
import { Point } from "./Point";
import { Question } from "./Question";
import { Stage } from "./Stage";
import { Triangle } from "./Triangle";

const PI_OVER_180: number = Math.PI / 180;
const PADDING_X: number = 80;
const PADDING_Y: number = 30;
const OUTER_LABEL_OFFSET: number = 16;
const LARGE_ANGLE_LABEL_OFFSET: number = 40;
const SMALL_ANGLE_LABEL_OFFSET: number = 80;
const TINY_ANGLE_LABEL_OFFSET: number = 90;
const ARC_LABEL_OFFSET: number = 40;
const FONT_LARGE: number = 22;
const FONT_MED: number = 18;
const FONT_SMALL: number = 14;
const DEGREES: string = 'Â°';

export class TriangleView {

    protected stage: Stage;
    protected triangle: Triangle;
    protected vertices: Array<Point> = [];
    protected vertexLabels: Array<Label> = [];
    protected sideLabels: Array<Label> = [];
    protected angleLabels: Array<Label> = [];
    protected arcs: Array<AngleArc> = [];
    protected scale: number = 1;
    protected orientation: Point = new Point(1, 1);
    protected question: Question;

    constructor(stage: Stage, question: Question) {
        this.stage = stage;
        this.question = question;
        this.triangle = this.question.getTriangle();
    }


    public resetQuestion(question: Question): void {
        this.question = question;
        this.triangle = this.question.getTriangle();
    }


    public render(): void {
        this.stage.clear();
        this.setup();

        // Translate
        this.stage.translate(this.getTranslation());

        // Get context
        const ctx = this.stage.context();

        // Angle arcs
        this.arcs[1].render(ctx);
        this.arcs[2].render(ctx);

        // Right angle
        this.renderRightAngle(ctx);

        // Sides
        this.renderSides(ctx);

        // Vertex labels
        this.vertexLabels[0].render(ctx);
        this.vertexLabels[1].render(ctx);
        this.vertexLabels[2].render(ctx);

        // Side labels
        this.sideLabels[0].render(ctx);
        this.sideLabels[1].render(ctx);
        this.sideLabels[2].render(ctx);

        // Angle labels
        this.angleLabels[1].render(ctx);
        this.angleLabels[2].render(ctx);
    }


    public flipX(): void {
        this.orientation.x *= -1;
    }


    public flipY(): void {
        this.orientation.y *= -1;
    }


    protected setup(): void {
        // Set scale
        this.scale = this.getScale();

        // Set initial verticies
        this.setupVertices();

        // Setup vertex labels
        this.vertexLabels = [
            new Label(new Point(this.vertices[0].x, this.vertices[0].y - (OUTER_LABEL_OFFSET * this.orientation.y)), 'A'),
            new Label(new Point(this.vertices[1].x + (OUTER_LABEL_OFFSET * this.orientation.x), this.vertices[1].y), 'B'),
            new Label(new Point(this.vertices[2].x, this.vertices[2].y + (OUTER_LABEL_OFFSET * this.orientation.y)), 'C')
        ];

        for (let i: number = 0; i < 3; i++) {
            this.vertexLabels[i].fontSize = FONT_LARGE;
            this.vertexLabels[i].active = true;
        }

        // Setup side labels
        this.sideLabels = [
            new Label(Point.mid(this.vertices[0], this.vertices[1]), this.triangle.side(0).toString() + ' km'),
            new Label(Point.mid(this.vertices[1], this.vertices[2]), this.triangle.side(1).toString() + ' km'),
            new Label(Point.mid(this.vertices[2], this.vertices[0]), this.triangle.side(2).toString() + ' km'),
        ];

        this.sideLabels[0].fontSize = FONT_SMALL;
        this.sideLabels[0].position.y -= OUTER_LABEL_OFFSET * this.orientation.y;

        this.sideLabels[1].fontSize = FONT_SMALL;
        this.sideLabels[1].textAlign = this.orientation.x === 1 ? 'left' : 'right';
        this.sideLabels[1].position.y += OUTER_LABEL_OFFSET * this.orientation.y;

        this.sideLabels[2].fontSize = FONT_SMALL;
        this.sideLabels[2].textAlign = this.orientation.x === 1 ? 'right' : 'left';
        this.sideLabels[2].position.x -= OUTER_LABEL_OFFSET * this.orientation.x;

        this.sideLabels[this.question.getDisplaySideIndex()].active = true;

        // Setup angle labels and arcs
        this.angleLabels = [];
        this.arcs = [];
        this.setupAngleLabels();

        // Clip Y axis to fit triangle
        const clip: number = this.triangle.bounds().y * this.scale + (PADDING_Y * 2);
        this.stage.clipY(clip);
    }


    protected setupVertices(): void {
        // Set initial verticies
        const bounds: Point = this.triangle.bounds();
        this.vertices = [
            new Point(0, 0),
            new Point(bounds.x * this.scale * this.orientation.x, 0),
            new Point(0, bounds.y * this.scale * this.orientation.y)
        ];
    }


    protected renderRightAngle(ctx: CanvasRenderingContext2D): void {
        const length: number = LARGE_ANGLE_LABEL_OFFSET;
        ctx.beginPath();
        ctx.lineWidth = 1;
        ctx.strokeStyle = "#000000";
        ctx.lineJoin = "round";
        ctx.moveTo(length * this.orientation.x, 0);
        ctx.lineTo(length * this.orientation.x, length * this.orientation.y);
        ctx.lineTo(0, length * this.orientation.y);
        ctx.stroke();
    }


    protected renderSides(ctx: CanvasRenderingContext2D): void {
        ctx.beginPath();
        ctx.lineWidth = 4;
        ctx.strokeStyle = "#000000";
        ctx.lineJoin = "round";
        ctx.moveTo(this.vertices[0].x, this.vertices[0].y);
        ctx.lineTo(this.vertices[1].x, this.vertices[1].y);
        ctx.lineTo(this.vertices[2].x, this.vertices[2].y);
        ctx.lineTo(this.vertices[0].x, this.vertices[0].y);
        ctx.closePath();
        ctx.stroke();
    }


    protected getScale(): number {
        const maxBounds: Point = this.stage.getBounds();
        maxBounds.x -= PADDING_X * 2;
        maxBounds.y -= PADDING_Y * 2;

        const triangleBounds: Point = this.triangle.bounds();

        if (triangleBounds.x > triangleBounds.y) {
            return maxBounds.x / triangleBounds.x;
        }

        return maxBounds.y / triangleBounds.y;
    }


    protected getTranslation(): Point {
        let minX: number = 0;
        let minY: number = 0;

        for (let i: number = 0; i < 3; i++) {
            if (this.vertices[i].x < minX) minX = this.vertices[i].x;
            if (this.vertices[i].y < minY) minY = this.vertices[i].y;
        }

        return new Point(Math.abs(minX) + PADDING_X, Math.abs(minY) + PADDING_Y);
    }


    protected setupAngleLabels(): void {
        // Angle 1
        let angle: number = this.triangle.angle(1);
        let offset: number = this.angleLabelOffset(1);
        let x: number = Math.cos(angle * 0.5 * PI_OVER_180) * offset;
        let y: number = Math.sin(angle * 0.5 * PI_OVER_180) * offset;
        this.angleLabels[1] = new Label(new Point(this.vertices[1].x - (x * this.orientation.x), this.vertices[1].y + (y * this.orientation.y)), this.triangle.angle(1).toString() + DEGREES);
        this.angleLabels[1].fontSize = FONT_MED;

        // Arc 1
        if (this.orientation.x === 1 && this.orientation.y === 1) {
            this.arcs[1] = new AngleArc(this.vertices[1], offset + ARC_LABEL_OFFSET, 180 - this.triangle.angle(1), 180);
        }
        else if (this.orientation.x === -1 && this.orientation.y === 1) {
            this.arcs[1] = new AngleArc(this.vertices[1], offset + ARC_LABEL_OFFSET, 0, this.triangle.angle(1));
        }
        else if (this.orientation.x === 1 && this.orientation.y === -1) {
            this.arcs[1] = new AngleArc(this.vertices[1], offset + ARC_LABEL_OFFSET, 180, 180 + this.triangle.angle(1));
        }
        else if (this.orientation.x === -1 && this.orientation.y === -1) {
            this.arcs[1] = new AngleArc(this.vertices[1], offset + ARC_LABEL_OFFSET, 360 - this.triangle.angle(1), 0);
        }

        // Angle 2
        angle = this.triangle.angle(2);
        offset = this.angleLabelOffset(2);
        x = Math.sin(angle * 0.5 * PI_OVER_180) * offset;
        y = Math.cos(angle * 0.5 * PI_OVER_180) * offset;
        this.angleLabels[2] = new Label(new Point(this.vertices[2].x + (x * this.orientation.x), this.vertices[2].y - (y * this.orientation.y)), this.triangle.angle(2).toString() + DEGREES);
        this.angleLabels[2].fontSize = FONT_MED;

        // Arc 2
        if (this.orientation.x === 1 && this.orientation.y === 1) {
            this.arcs[2] = new AngleArc(this.vertices[2], offset + ARC_LABEL_OFFSET, 270, 270 + this.triangle.angle(2));
        }
        else if (this.orientation.x === -1 && this.orientation.y === 1) {
            this.arcs[2] = new AngleArc(this.vertices[2], offset + ARC_LABEL_OFFSET, 270 - this.triangle.angle(2), 270);
        }
        else if (this.orientation.x === 1 && this.orientation.y === -1) {
            this.arcs[2] = new AngleArc(this.vertices[2], offset + ARC_LABEL_OFFSET, 90 - this.triangle.angle(2), 90);
        }
        else if (this.orientation.x === -1 && this.orientation.y === -1) {
            this.arcs[2] = new AngleArc(this.vertices[2], offset + ARC_LABEL_OFFSET, 90, 90 + this.triangle.angle(2));
        }

        // Set visibility
        const index: number = this.question.getAngleDisplayIndex();
        this.angleLabels[index].active = true;
        this.arcs[index].active = true;
    }


    protected angleLabelOffset(index: number): number {
        const angle: number = this.triangle.angle(index);
        if (angle > 45) return LARGE_ANGLE_LABEL_OFFSET;
        if (angle > 30) return SMALL_ANGLE_LABEL_OFFSET;

        return TINY_ANGLE_LABEL_OFFSET;
    }
}
