import { Point } from "./classes/Point";
import { Question } from "./classes/Question";
import { Stage } from "./classes/Stage";
import { TriangleView } from "./classes/TriangleView";

document.addEventListener('DOMContentLoaded', (event) => {

    const rootEl: HTMLElement | null = document.getElementById('rats');
    const canvasEl: HTMLCanvasElement = document.createElement('canvas');
    rootEl?.appendChild(canvasEl);
    const ctx: CanvasRenderingContext2D | null = canvasEl.getContext('2d');

    if (rootEl && ctx) {
        // Setup stage
        const stage: Stage = new Stage(rootEl, canvasEl, ctx, new Point(1, 1));

        let question: Question = new Question();
        const triangleView: TriangleView = new TriangleView(stage, question);
        if (Math.random() < 0.5) triangleView.flipX();
        if (Math.random() < 0.5) triangleView.flipY();
        triangleView.render();

        // UI Wrapper
        const wrapper: HTMLElement = document.createElement('div');
        wrapper.setAttribute('style', 'margin-left:60px; margin-right:60px');
        rootEl.appendChild(wrapper);

        // Question text
        const questionText: HTMLElement = document.createElement('p');
        questionText.innerHTML = question.text();
        wrapper.appendChild(questionText);

        // Question input
        const inputGroup: HTMLElement = document.createElement('div');
        inputGroup.className = 'input-group mb-3';

        const inputText: HTMLInputElement = document.createElement('input');
        inputText.className = 'form-control';
        inputText.placeholder = "Enter your answer...";
        inputGroup.appendChild(inputText);

        const unit: HTMLElement = document.createElement('span');
        unit.className = 'input-group-text';
        unit.innerText = 'km';
        inputGroup.appendChild(unit);

        const answerButton: HTMLButtonElement = document.createElement('button');
        answerButton.innerText = 'Submit';
        answerButton.className = 'btn btn-success';
        inputGroup.appendChild(answerButton);

        wrapper.appendChild(inputGroup);

        answerButton.addEventListener('click', (e: Event) => {

            if (inputText.value.length === 0) return;

            if (question.checkAnswer(inputText.value)) {
                userMessage.className = 'alert alert-success';
                userMessage.innerHTML = 'Correct, well done!';
                nextQuestionButton.classList.remove('d-none');
                inputGroup.classList.add('d-none');
            }
            else {
                userMessage.className = 'alert alert-danger';
                userMessage.innerHTML = 'Oops, not quite the answer we were looking for!';
            }
        });

        // User meassage
        const userMessage: HTMLElement = document.createElement('div');
        wrapper.appendChild(userMessage);

        // Next question
        const nextQuestionButton: HTMLButtonElement = document.createElement('button');
        nextQuestionButton.innerText = 'Next question';
        nextQuestionButton.className = 'btn btn-primary d-none';
        wrapper.appendChild(nextQuestionButton);

        nextQuestionButton.addEventListener('click', (e: Event) => {
            userMessage.classList.add('d-none');
            nextQuestionButton.classList.add('d-none');
            inputGroup.classList.remove('d-none');
            inputText.value = '';
            question = new Question();
            questionText.innerHTML = question.text();
            triangleView.resetQuestion(question);
            if (Math.random() < 0.5) triangleView.flipX();
            if (Math.random() < 0.5) triangleView.flipY();
            triangleView.render();
        });
    }
});
