import { Point } from "./Point";

const PI_OVER_180: number = Math.PI / 180;

export class Triangle {

    protected sides: Array<number> = [0, 0, 0];
    protected angles: Array<number> = [90, 0, 0];

    constructor(angle: number, length: number) {
        // Set angles;
        this.angles[1] = angle;
        this.angles[2] = 90 - angle;

        // Set sides
        this.sides[0] = length * Math.cos(angle * PI_OVER_180);
        this.sides[1] = length;
        this.sides[2] = length * Math.sin(angle * PI_OVER_180);
    }


    public rationaliseOneSide(index: number): void {
        let i: number;

        // Set other sides length to zero
        for (i = 0; i < 3; i++) {
            if (i !== index) this.sides[i] = 0;
        }

        // Round remaining side
        this.sides[index] = Math.round(this.sides[index]);

        // Calculate other side lengths
        for (i = 0; i < 3; i++) {
            if (this.sides[i] === 0) {
                this.sides[i] = this.sideLength(i);
            }
        }
    }


    public side(index: number): number {
        return this.sides[index];
    }


    public angle(index: number): number {
        return this.angles[index];
    }


    public bounds(): Point {
        return new Point(this.sides[0], this.sides[2]);
    }


    public validate(n: number = 8): boolean {
        const ab: number = this.sides[0] * this.sides[0] + this.sides[2] * this.sides[2];
        const c: number = this.sides[1] * this.sides[1];

        return ab.toFixed(n) === c.toFixed(n);
    }


    protected sideLength(index: number): number {
        if (index === 0) {
            if (this.sides[1] !== 0) {
                // S0 = S1 * COS (A1)
                return this.sides[1] * Math.cos(this.angles[1] * PI_OVER_180);
            }

            // S0 = S2 / TAN (A1)
            return this.sides[2] / Math.tan(this.angles[1] * PI_OVER_180);
        }

        if (index === 1) {
            if (this.sides[0] !== 0) {
                // S1 = S0 / COS (A1)
                return this.sides[0] / Math.cos(this.angles[1] * PI_OVER_180);
            }

            // S1 = S2 / SIN (A1)
            return this.sides[2] / Math.sin(this.angles[1] * PI_OVER_180);
        }

        // Assume index === 2
        if (this.sides[0] !== 0) {
            // S2 = S0 * TAN (A1)
            return this.sides[0] * Math.tan(this.angles[1] * PI_OVER_180);
        }

        // S2 = S1 * SIN (A1)
        return this.sides[1] * Math.sin(this.angles[1] * PI_OVER_180);
    }
}
