import { MAX_ANGLE, MAX_LENGTH, MIN_ANGLE, MIN_LENGHT } from "../constants/config";
import { Triangle } from "./Triangle";

export class Question {

    protected triangle: Triangle;
    protected displaySideIndex: number = 0;
    protected answerSideIndex: number = 0;
    protected displayAngleIndex: number = 1;

    constructor() {
        this.triangle = this.generateTriangle();
    }

    public getTriangle(): Triangle {
        return this.triangle;
    }

    public getDisplaySideIndex(): number {
        return this.displaySideIndex;
    }


    public getAngleDisplayIndex(): number {
        return this.displayAngleIndex;
    }


    public text(): string {
        let questionText: string = "ABC is a right-angled triangle.<br>";
        questionText += "Calculate the length of " + this.answerSideLabel() + ".<br>";
        questionText += "Give your answer correct to 3 significant figures.";

        return questionText;
    }


    public checkAnswer(answer: string): boolean {
        const correctAnswer: number = this.triangle.side(this.answerSideIndex);
        console.log("Correct: " + correctAnswer.toPrecision(3));

        return answer === correctAnswer.toPrecision(3);
    }


    protected generateTriangle(): Triangle {
        const angle: number = Math.floor(Math.random() * (MAX_ANGLE - MIN_ANGLE)) + MIN_ANGLE;
        const length: number = Math.random() * (MAX_LENGTH - MIN_LENGHT) + MIN_LENGHT;
        const triangle: Triangle = new Triangle(angle, length);

        // Set display side
        this.displaySideIndex = Math.floor(Math.random() * 3);
        triangle.rationaliseOneSide(this.displaySideIndex);

        // Answer side must be different to display side
        this.answerSideIndex = this.displaySideIndex;
        while (this.answerSideIndex === this.displaySideIndex) {
            this.answerSideIndex = Math.floor(Math.random() * 3);
        }

        // Display angle index
        this.displayAngleIndex = Math.floor(Math.random() * 2) + 1;

        return triangle;
    }


    protected answerSideLabel(): string {
        if (this.answerSideIndex === 0) return 'AB';
        if (this.answerSideIndex === 1) return 'BC';

        return 'CA';
    }

}
